<template>
  <div id="canvas"></div>
</template>

<script>
import * as Matter from 'matter-js'

export default {
  mounted() {
    if (process) {
      const canvasWidth = document.getElementById('footer').clientWidth
      const canvasHeight = 80

      // module aliases
      const Engine = Matter.Engine,
        Render = Matter.Render,
        World = Matter.World,
        Bodies = Matter.Bodies

      // create an engine
      const engine = Engine.create()

      // create a renderer
      const render = Render.create({
        element: document.getElementById('canvas'),
        engine: engine,
        options: {
          width: canvasWidth,
          height: canvasHeight,
          background: 'rgba(0, 0, 0, 0)',
          wireframes: false,
        },
      })

      // eslint-disable-next-line prefer-const
      let objects = []
      const offsetX = 50

      // create ground
      objects.push(
        Bodies.rectangle(canvasWidth / 2, canvasHeight, canvasWidth, 10, {
          isStatic: true,
        }),
      )

      // create slope
      const triangleLength = 60
      const triangle = Bodies.polygon(
        offsetX,
        canvasHeight,
        3, // triangle
        triangleLength,
        {
          isStatic: true,
        },
      )
      Matter.Body.rotate(triangle, 3.14)
      objects.push(triangle)

      // create domino
      const dominoStartPosX = offsetX + triangleLength + 50
      const dominoDistance = 40
      const dominoCnt = canvasWidth / dominoDistance
      for (let i = 0; i < dominoCnt; i++) {
        objects.push(
          Bodies.rectangle(dominoStartPosX + i * dominoDistance, 0, 7, 60, {
            density: 0.02,
            friction: 1,
          }),
        )
      }

      // create sphere
      const circle = Matter.Bodies.circle(offsetX, 0, 20, {
        density: 0.02,
      })
      objects.push(circle)

      // add all of the bodies to the world
      World.add(engine.world, objects)

      // run the engine
      Engine.run(engine)

      // run the renderer
      Render.run(render)
    }
  },
}
</script>
